body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@font-face {
    font-family: "Montserrat-Bold";
    src: url(/calendar/static/media/Montserrat-Bold.fe7c3f26.woff) format("woff");
}

@font-face {
    font-family: "Montserrat-ExtraBold";
    src: url(/calendar/static/media/Montserrat-ExtraBold.9bfcc487.woff) format("woff");
}

@font-face {
    font-family: "Montserrat-Regular";
    src: url(/calendar/static/media/Montserrat-Regular.9b4b6e81.woff) format("woff");
}

.calendar-container {
    margin-top: 96px;
    margin-left: auto;
    margin-right: auto;
}

.calendar {
    border-spacing: 0;
    border-collapse: collapse;
    width: 100%;
}

.calendar-header {
    font-size: 24px;
    background-color: #0B3157;
}

.calendar thead {
    background: #0B3157;
    width: 100%;
}

.calendar td {
    width: 10%;
}

.week-day {
    text-align: center;
    vertical-align: center;
    height: 40px;
}

.week-day {
    text-transform: uppercase;
    text-align: center;
    color: #FFFFFF;
    opacity: 0.5;
    font-size: 12px;
    font-family: Montserrat-Regular;
}

.day {
    background: #FFFFFF;
    cursor: pointer;
    line-height: 45px;
    font-size: 16px;
    font-family: Montserrat-Bold;
    color: #0B3157;
    margin: 1%;
}

.row {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100%;
}

.day {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex: 1 1;
            flex: 1 1;
    height: 100%;
}

.day-number {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    line-height: 50px;
}

.different-month {
    opacity: 0;
}

.diff-month {
    opacity: 1;
}

